<style lang="scss">
    //vars
    @import 'src/assets/styles/settings.scss';

    .copyright-block{
        position: absolute;
        bottom: 4%;
        right: 3%;
        font-family: bebas;
        font-weight: 800;
        font-size: 20px;
        .copyright{
            display: inline-block;
            margin-right: 60px;
        }
        .copyright-link{
            display: inline-block;
            a{
                color: $black;
            }
        }
    }

    .modal-view{
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0px;
        left: 0px;
        z-index: 3;
        background-color: $main;
        .content{
            position: absolute;
            top: 50%; left: 50%;
            transform: translate(-50%, -50%);
            width: 80%;
            height: 95%;
        }
        .main-nav{
            margin: 0px;
            padding: 0px;
            list-style: none;
            width: 35%;
            display: inline-block;
            li{
                margin-bottom: 5px;
                position: relative;
                &:hover{
                    a{
                        color: $orange!important;
                    }
                }
                &:nth-child(3){
                    &:hover{
                        a{
                            &:after{
                                opacity: 1;
                            }
                        }
                        .second-nav{
                            opacity: 1;
                        }
                    }
                    a{
                        &:after{
                            content: '';
                            position: absolute;
                            border: 12px solid $orange;
                            border-color: transparent transparent transparent $orange;
                            top: 50%;
                            transform: translateY(-50%);
                            right: -50px;
                            opacity: 0;
                            transition: all .35s; 
                        }
                    }
                }
                a{
                    color: $black!important;
                    font-family: bebas;
                    font-weight: 800;
                    font-size: 5vw;
                    line-height: 100%;
                    transition: all .35s;
                    position: relative;
                   
                }
            }
            .second-nav{
                list-style: none;
                padding: 0px;
                margin: 0px;
                position: absolute;
                top: 10px;
                left: 100%;
                width: 100%;
                opacity: 0;
                transition: all .35s;
                li{
                    a{
                        font-size: 48px;
                        opacity: 0.6;
                        text-decoration: none;
                        color: $black!important;
                        &:hover{
                            color: $orange!important;
                        }
                        &:after{
                            content: none;
                        }
                    }
                }
            }
        }
    }

    .modal-view .socials-block{
        display: none;
    }

    @media (max-width: 1367px){

        .modal-view .main-nav li a{
            font-size: 4.6vw;
        }
        .modal-view .main-nav .second-nav li a{
            font-size: 32px;
        }
    }

    @media (max-width: 1025px){
        .modal-view .main-nav li a{
            font-size: 6.9vw;
        }
        .modal-view .main-nav{
            width: 40%;
        }
        .modal-view .content{
            width: 75%;
        }
    }

    @media(max-width: 769px){
        
        .modal-view .content{
            height: 75%;
        }

        .modal-view .main-nav {
            width: 45%;
        }

        .modal-view .socials-block{
            display: block;
            position: absolute;
            right: 3%;
            top: 3%;
            ul{
                padding: 0px;
                margin: 0px;
                list-style: none;
                li{
                    margin-bottom: 20px;
                    width: 28px;
                    text-align: center;
                    a{
                        color: $side-menu;
                        .fa{
                            font-size: 20px;
                        }
                    }
                }
            }
        }
    }

    @media (max-width: 469px){
        .modal-view .content{
            width: 94%;
            height: 80%;
            overflow: scroll;
        }
        .modal-view .main-nav{
            width: 80%;
        }
        .modal-view .main-nav li a{
            font-size: 12vw;
        }
        .modal-view .main-nav .second-nav{
            position: relative;
            height: 0%;
            top: 0px;
            left: 0px;
            opacity: 1;
            height: auto;
            overflow: hidden;
        }
        .modal-view .main-nav li:nth-child(3):hover .second-nav{
            height: 270px;
        }
        .second-nav li:first-child{
            margin-top: 12px;
        }
        .modal-view .main-nav .second-nav li a{
            font-size: 26px;
        }
        .modal-view .main-nav{
            overflow-y: scroll;
        }
    }

    /* Coded by Lorenzo Bocchi ~ www.be.net/LorenzoBocchi */
/* Designed by CSS Design Awards ~ www.cssdesignawards.com */

/* purple: #673AB7 */
/* light: #EDEDED */
/* black: #000000 */

#cssda-badge{
  /* monogram position here */
  position: absolute;
  bottom: 100%;
  right: 0px;
  margin-bottom: 30px;
  /*  */
  width: 90px;
  height: 90px;
  border-radius: 90px;
  background: #EDEDED;
  -webkit-transition: all .25s cubic-bezier(.615,.19,.305,.91);
  -moz-transition: all .25s cubic-bezier(.615,.19,.305,.91);
  transition: all .25s cubic-bezier(.615,.19,.305,.91);
  cursor: pointer;
}
#cssda-badge:hover{
  -webkit-animation: bounce .75s linear both;
  -moz-animation: bounce .75s linear both;
  animation: bounce .75s linear both;
  background: #673AB7;
}
#cssda-badge svg {
  position: absolute;
  width: 90px;
  height: 90px;
  fill: #000000;
}
#cssda-badge:hover svg{
  fill: #EDEDED;
}
.cssda-badge-content{
  position: absolute;
  width: 90px;
  height: 90px;
  -webkit-transition: all .12s cubic-bezier(.615,.19,.305,.91);
  -moz-transition: all .12s cubic-bezier(.615,.19,.305,.91);
  transition: all .12s cubic-bezier(.615,.19,.305,.91);
}
#cssda-badge:hover .cssda-badge-content{
  -webkit-transform: rotate(360deg);
  -moz-transform: rotate(360deg);
  transform: rotate(360deg);
}
#cssda-badge:hover #cssda-badge-logo{
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  transform: scale(1.1);
}

@media only screen and (max-width: 700px) {
  #cssda-badge{
    /* monogram responsive position here */
    /*  */
    width: 60px;
    height: 60px;
    display: none;
  }
  #cssda-badge svg {
    width: 60px;
    height: 60px;
  }
  #cssda-badge-logo{
    -webkit-transform: scale(1.35);
    -moz-transform: scale(1.35);
    transform: scale(1.35);
  }
  #cssda-badge:hover #cssda-badge-logo{
    -webkit-transform: scale(1.5);
    -moz-transform: scale(1.5);
    transform: scale(1.5);
  }
  #cssda-badge-title{
    display: none;
  }
  #cssda-badge-award{
    display: none;
  }
}

